.about {
  margin-bottom: 2rem;
  width: 100%;
  display: flex;
  flex-direction: column;
}
.aboutBottom {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 5px;
}
.about .aboutBottom .heade {
  padding: 16px;
  font-weight: 400;
  font-size: 50px;
  color: black;
  height: 30px;
  display: flex;
  justify-content: center;
  transform: translateY(-40px);
}

@media only screen and (max-width: 650px) {
  .about .aboutBottom .heade {
    font-size: 24px;
  }
  .aboutBottom {
    height: 84%;
    margin-top: 0;
  }
  .aboutTop {
    height: 35%;
  }
}
  
 